.dog-img{
    width: 300px;
    height: 250px;
}

.input{
    width: 600px;
    height: 50px;
    min-width: 100px;
    min-height: 50px;
    font-family: Georgia, 'Times New Roman', Times, serif;

}



.dogsList{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

@media only screen and (max-width: 600px)  {
    
    .dog-img {
        max-width: 120px;
        max-height: 120px;
    }

    .input{
        width: 200px;
        height: 35px;
    }
    



}